/* Safari Style Section */
.safari-style-section {
  background: #ffffff;
  padding: 0;
  position: relative;
  width: 100%;
}

.safari-style-container {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  padding: 0 184px;
  position: relative;
}

/* Header */
.safari-style-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 33px;
}

.safari-style-title {
  font-family: 'Georgia', serif;
  font-size: 50px;
  font-weight: 400;
  line-height: 64px;
  letter-spacing: 0.2px;
  color: #030922;
  margin: 0;
}

.safari-style-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
}

.safari-style-icon svg {
  width: 28px;
  height: 25px;
}

/* Description */
.safari-style-description {
  font-family: 'Inter', sans-serif;
  font-size: 19px;
  font-weight: 300;
  line-height: 32px;
  letter-spacing: 0.66px;
  color: #030922;
  margin: 0 0 136px 0;
  max-width: 595px;
}

/* Featured Lodge */
.featured-lodge {
  position: relative;
  width: 100%;
  height: 755px;
  margin-bottom: 273px;
  border-radius: 0;
  overflow: hidden;
}

.featured-lodge-image {
  position: relative;
  width: 100%;
  height: 100%;
}

.featured-lodge-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.featured-lodge-content {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 496px;
  height: 469px;
  background: #030922;
  padding: 60px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.featured-lodge-name {
  font-family: 'Georgia', serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 41px;
  color: #FCF8F7;
  margin: 0 0 9px 0;
}

.featured-lodge-location {
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #FCF8F7;
  margin: 0 0 42px 0;
}

.featured-lodge-description {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 29px;
  letter-spacing: 1px;
  color: #FCF8F7;
  margin: 0;
  max-width: 372px;
}

/* Safari Lodges Grid */
.safari-lodges-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin-bottom: 91px;
}

.safari-lodge-card {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.lodge-image-container {
  position: relative;
  width: 100%;
  height: 638px;
  background: #FCF8F7;
  margin-bottom: 10px;
}

.lodge-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/* Review Badge */
.review-badge {
  position: absolute;
  bottom: 12px;
  right: 12px;
  padding: 6px 12px;
  background: #FAF1EE;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  z-index: 5;
  transform: none;
  box-shadow: 0 6px 18px rgba(3, 9, 34, 0.12);
}

.featured-lodge .review-badge {
  bottom: 20px;
  right: 20px;
}

.review-badge span {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 17px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #030922;
}

/* View Button */
.view-button {
  position: absolute;
  bottom: 144px;
  left: 36px;
  width: 108px;
  height: 108px;
  border-radius: 54px;
  background: radial-gradient(70.71% 70.71% at 50% 50%, rgba(3, 9, 34, 0.77) 0%, #030922 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.view-button:hover {
  transform: scale(1.05);
}

/* Reveal view button only on card hover */
.safari-lodge-card:hover .view-button,
.lodge-image-container:hover .view-button {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.view-button span {
  font-family: 'Inter', sans-serif;
  font-size: 15px;
  font-weight: 300;
  line-height: 16px;
  letter-spacing: 0.788px;
  text-transform: uppercase;
  color: #ffffff;
}

/* Lodge Info */
.lodge-info {
  padding: 0;
}

.lodge-name {
  font-family: 'Georgia', serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 41px;
  color: #030922;
  margin: 0 0 7px 0;
}

.lodge-location {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #787878;
  margin: 0;
}

/* View More Section */
.view-more-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 92px;
}

.view-more-divider {
  height: 1px;
  border-top: 1px solid #D9D9D9;
  flex: 1;
  max-width: 840px;
}

.view-more-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 62px;
  border: 1px solid rgba(217, 217, 217, 0.5);
  background: transparent;
  font-family: 'Inter', sans-serif;
  font-size: 15px;
  font-weight: 300;
  line-height: 16px;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  color: #030922;
  cursor: pointer;
  transition: all 0.3s ease;
}

.view-more-button:hover {
  background: #030922;
  color: #ffffff;
  border-color: #030922;
}

/* Responsive Styles */

/* Large Desktop */
@media (max-width: 1440px) {
  .safari-style-container {
    padding: 0 120px;
  }
  
  .safari-style-title {
    font-size: 45px;
    line-height: 58px;
  }
  
  .safari-style-description {
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 100px;
  }
  
  .featured-lodge {
    height: 650px;
    margin-bottom: 200px;
  }
  
  .featured-lodge-content {
    width: 420px;
    height: 400px;
    padding: 50px;
  }
  
  .featured-lodge-name {
    font-size: 28px;
    line-height: 36px;
  }
  
  .featured-lodge-description {
    font-size: 16px;
    line-height: 26px;
  }
  
  .safari-lodges-grid {
    gap: 30px;
  }
  
  .lodge-image-container {
    height: 550px;
  }
  
  .review-badge {
    bottom: 20px;
  }
  
  .lodge-name {
    font-size: 28px;
    line-height: 36px;
  }
}

/* Tablet Styles */
@media (max-width: 1024px) {
  .safari-style-container {
    padding: 0 60px;
  }
  
  .safari-style-title {
    font-size: 40px;
    line-height: 50px;
  }
  
  .safari-style-description {
    font-size: 17px;
    line-height: 28px;
    margin-bottom: 80px;
  }
  
  .featured-lodge {
    height: 500px;
    margin-bottom: 120px;
  }
  
  .featured-lodge-content {
    width: 100%;
    height: 300px;
    padding: 40px;
    position: relative;
    background: rgba(3, 9, 34, 0.9);
  }
  
  .featured-lodge-name {
    font-size: 26px;
    line-height: 34px;
  }
  
  .featured-lodge-description {
    font-size: 15px;
    line-height: 24px;
  }
  
  .safari-lodges-grid {
    grid-template-columns: 1fr;
    gap: 60px;
    margin-bottom: 60px;
  }
  
  .lodge-image-container {
    height: 400px;
  }
  
  .review-badge {
    bottom: 12px;
    right: 12px;
  }
  
  .view-button {
    bottom: 80px;
    left: 20px;
    width: 80px;
    height: 80px;
  }
  
  .view-button span {
    font-size: 13px;
  }
  
  .lodge-name {
    font-size: 26px;
    line-height: 34px;
  }
  
  .view-more-section {
    margin-bottom: 60px;
  }
  
  .view-more-divider {
    max-width: 200px;
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .safari-style-container {
    padding: 0 20px;
  }
  
  .safari-style-header {
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .safari-style-title {
    font-size: 32px;
    line-height: 40px;
  }
  
  .safari-style-description {
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 60px;
    text-align: center;
  }
  
  .featured-lodge {
    height: 400px;
    margin-bottom: 80px;
  }
  
  .featured-lodge-content {
    height: 250px;
    padding: 30px 20px;
  }
  
  .featured-lodge-name {
    font-size: 22px;
    line-height: 28px;
  }
  
  .featured-lodge-location {
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .featured-lodge-description {
    font-size: 14px;
    line-height: 22px;
  }
  
  .lodge-image-container {
    height: 300px;
  }
  
  .review-badge {
    bottom: 12px;
    right: 12px;
    padding: 4px 10px;
    width: auto;
    height: auto;
  }
  
  .review-badge span {
    font-size: 12px;
  }
  
  .view-button {
    bottom: 60px;
    left: 15px;
    width: 60px;
    height: 60px;
  }
  
  .view-button span {
    font-size: 11px;
  }
  
  .lodge-name {
    font-size: 22px;
    line-height: 28px;
  }
  
  .lodge-location {
    font-size: 12px;
  }
  
  .view-more-section {
    flex-direction: column;
    gap: 15px;
    margin-bottom: 40px;
  }
  
  .view-more-divider {
    width: 100%;
    max-width: none;
  }
  
  .view-more-button {
    width: 160px;
    height: 50px;
    font-size: 13px;
  }
}

/* Extra small mobile */
@media (max-width: 480px) {
  .safari-style-container {
    padding: 0 15px;
  }
  
  .safari-style-title {
    font-size: 26px;
    line-height: 34px;
  }
  
  .safari-style-description {
    font-size: 15px;
    line-height: 24px;
  }
  
  .featured-lodge {
    height: 350px;
    margin-bottom: 60px;
  }
  
  .featured-lodge-content {
    height: 200px;
    padding: 20px 15px;
  }
  
  .featured-lodge-name {
    font-size: 20px;
    line-height: 26px;
  }
  
  .featured-lodge-description {
    font-size: 13px;
    line-height: 20px;
  }
  
  .lodge-image-container {
    height: 250px;
  }
  
  .lodge-name {
    font-size: 20px;
    line-height: 26px;
  }
  
  .view-more-button {
    width: 140px;
    height: 45px;
    font-size: 12px;
  }
}
