/* About Section */
.about-section { background: #ffffff; min-height: 100vh; display: flex; justify-content: center; align-items: center; padding: 120px 0; }
.about-content { max-width: 1553px; width: 100%; padding: 0 184px; text-align: center; position: relative; }
.about-logo { display: flex; justify-content: center; align-items: center; width: 110px; height: 80px; margin: 0 auto 39px; }
.about-title { font-family: 'Georgia', serif; font-size: 40px; font-weight: 400; line-height: 51.56px; letter-spacing: .2px; color: #030922; margin: 0 0 56px; max-width: 916px; margin-left: auto; margin-right: auto; }
.about-badges { display: flex; justify-content: center; align-items: center; gap: 30px; margin-top: 56px; position: relative; }
.about-badges::before { content: ''; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100vw; height: 1px; background: #D9D9D9; border-top: 1px solid #D9D9D9; z-index: 1; }
.badge { display: flex; justify-content: center; align-items: center; width: 225px; height: 224px; background: #fff; position: relative; z-index: 2; }
.badge img, .badge svg { width: 100%; height: 100%; object-fit: contain; }

@media (max-width:1440px){ .about-content{padding:0 120px;} .about-title{font-size:36px;line-height:46px;} }
@media (max-width:1024px){ .about-content{padding:0 60px;} .about-title{font-size:32px;line-height:42px;margin-bottom:40px;} .about-badges{gap:20px;} .badge{width:180px;height:180px;} }
@media (max-width:768px){ .about-section{padding:80px 0;} .about-content{padding:0 20px;} .about-logo{width:80px;height:60px;margin-bottom:30px;} .about-title{font-size:24px;line-height:32px;margin-bottom:30px;} .about-badges{flex-direction:column;gap:20px;} .about-badges::before{display:none;} .badge{width:150px;height:150px;} }
@media (max-width:480px){ .about-content{padding:0 15px;} .about-title{font-size:20px;line-height:28px;} .about-logo{width:60px;height:45px;} .badge{width:120px;height:120px;} }
